<script setup>
    import { onMounted, ref,inject,watch,nextTick } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    import Multiselect from '@suadelabs/vue3-multiselect';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    import '@/assets/sass/scrollspyNav.scss';
    import { useRoute } from 'vue-router'

      const user_permissions = JSON.parse(localStorage.getItem("user")).permissions;

const route = useRoute()
    const selected_service = ref('');
    const current_km_of_service = ref('');
    const next_km_of_service = ref('');
    const loading_spinner = ref(true);
    const jobcard_registration = ref(false);
    const loader = ref(false);
    const services = ref([]);
    const showMessage = inject('showMessage');
    const showError = inject('showError');
    const formatNumber = inject('formatNumber')
    const tableShimmer = inject('tableShimmer');
    useMeta({ title: 'Jobcard Lists' });
    const jobcardData = ref ([])
const jobcardSummary = async (id) => {
  loader.value = true;
    try {
        const response = await axiosInstance.get(`/jobcard-summary?asset_id=${id}`);
        jobcardData.value = response.data;

        await nextTick();
        printJobcardSummary();
        
    } catch (error) {
        showError(error.message);
        console.error("Error fetching jobcard summary:", error);
    }
};

const printJobcardSummary = () => {
  const jobcards = jobcardData.value || [];

  loader.value = false;
  const iframe = document.createElement("iframe");
  iframe.style.display = "none";
  document.body.appendChild(iframe);

  const printContent = `
<!DOCTYPE html>
<html>
<head>
<title>Jobcard Summary</title>
<style>
  body { font-family: Arial, sans-serif; margin: 40px; font-size: 13px; }
  .header { display: flex; justify-content: space-between; align-items: flex-start; margin-bottom: 10px; }
  .logo { width: 150px; height: auto; }
  .company-title { text-align: center; margin-top: 5px; }
  .company-title h2 { margin: 5px 0; }
  .reg-info { position: absolute; top: 40px; right: 40px; text-align: right; font-size: 10px; line-height: 1.4; font-weight: normal; }
  table { width: 100%; border-collapse: collapse; margin-top: 20px; }
  th, td { border: 1px solid #000; padding: 6px; text-align: center; }
  th { background-color: #f8f8f8; }
</style>
</head>
<body>

<div class="header">
  <img src="https://bushman.ink/copex.jpg" class="logo" />
  <div class="reg-info">
    TIN: 135-489-247<br>
    VRN: 40-032826C
  </div>
</div>

<div class="company-title">
  <h2>COPEX CONTRACTORS CO.LTD</h2>
  <div>Msamvu, P.O.BOX 678, Morogoro</div>
  <div>Mobile: 0713 000 777, 0657 111 555</div>
</div>


<table>
  <thead>
    <tr>
      <th>${asset.value.category.name}</th>
      <th>TYPE</th>
    </tr>
  </thead>
  <tbody>
      <tr>
        <td>${asset.value.name}</td>
        <td>${asset.value.type_name}</td>
      </tr>
  </tbody>
</table>

<h2 style="text-align:center; margin-top: 20px;">Jobcard Summary</h2>

<table>
  <thead>
    <tr>
      <th>SN</th>
      <th>Jobcard NO</th>
      <th>Date Created</th>
      <th>Service Type</th>
      <th>Current Km Service</th>
      <th>Next Km Service</th>
      <th>Status</th>
      <th>Total Sum</th>
    </tr>
  </thead>
  <tbody>
    ${jobcards.map((jc, index) => `
      <tr>
        <td>${index + 1}</td>
        <td>${jc.id}</td>
        <td>${jc.date_created}</td>
        <td>${jc.service_type}</td>
        <td>${jc.current_km_service}</td>
        <td>${jc.next_km_service}</td>
        <td>${jc.status_name}</td>
        <td style="text-align:right">${parseFloat(jc.total_sum).toLocaleString()}/=</td>
      </tr>
    `).join("")}
    <tr>
      <td colspan="7" style="text-align:right"><strong>Grand Total</strong></td>
      <td style="text-align:right"><strong>${
        jobcards.reduce((sum, jc) => sum + parseFloat(jc.total_sum || 0), 0).toLocaleString()
      }/=</strong></td>
    </tr>
  </tbody>
</table>




</body>
</html>
`;

  const iframeDoc = iframe.contentWindow.document;
  iframeDoc.open();
  iframeDoc.write(printContent);
  iframeDoc.close();

  iframe.onload = () => {
    iframe.contentWindow.focus();
    iframe.contentWindow.print();
    setTimeout(() => {
      document.body.removeChild(iframe);
    }, 1000);
  };
};

const serviceTitle = ref('')
const getServices = (title) =>{
    serviceTitle.value = title.toUpperCase()
}

    const columns = ref(['Sno','card', 'name','date_created','service_type','current_km_of_service','next_km_of_service','status','actions']);
    const sparesColumns = ref(['Sno','jobcard_id', 'service_type','spare','description','quantity','rate','amount']);
    const tyresColumns = ref(['Sno','jobcard_id', 'service_type','tyre_no','quantity','rate','amount']);
    const costNoteColumns = ref(['Sno','jobcard_id', 'service_type','description','quantity','rate','amount']);
    const items = ref([]);
    const table_option = ref({
        perPage: 50,
        perPageValues: [ 50,100,200,500,1000],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['id', 'vehicle_no','date','service_type','current_service_kms','next_service_kms','status'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });

   const header = ref('JOBCARDS')
  const fetchJobCards = async() => {
   try{
     const response = await axiosInstance.get(`job-card?asset_id=${route.params.asset_id}`)
     items.value = response.data.map((item,index)=>({
      ...item,
      Sno : index + 1,
      card : item.id,
      name : item.machine.name + ' - ' + item.machine.type_name,
      status : {
        key : item.status.name,
        class : item.status.color
      }
     }))
     filteredJobcards.value = items.value
   }catch(error){

   }
};


    const date_created = ref('')
    const registerJobcard = () => {
        jobcard_registration.value = false;
        loading_spinner.value = true;
        const jobcardData = {
            machine_id: route.params.asset_id,
            machine_name: "none",
            status_name: 'Active',
            status_color: 'primary',
            status_id : 6,
            service_type: services.value.find(service => service.id === selected_service.value).name,
            current_km_of_service: current_km_of_service.value,
            next_km_of_service: next_km_of_service.value ,
            date_created : date_created.value,
            company_id : company.value.id,
            issuer_id : JSON.parse(localStorage.getItem("user")).id,
        };

        axiosInstance
            .post('/job-card', jobcardData)
            .then((response) => {
                fetchJobCards();
                showMessage('Jobcard registered successfully!');
                loading_spinner.value = false;
            })
            .catch((error) => {
                showError(error.message);
                console.log(error);
                loading_spinner.value = false;
            });
    };

const fetchServices = () =>{
      loading_spinner.value = true;
      axiosInstance
        .get(`/service-type`)
        .then((response) => {
          if (Array.isArray(response.data)) {
            services.value = response.data;
          } else {
            console.error("Invalid response format");
          }
        })
        .catch((error) => {
          console.error("Error fetching user data:", error.message);
        })
        .finally(() => {
          loading_spinner.value = false;
        });
    }


const deleteJobcard = (jobcardId) => {
    if (confirm('Are you sure you want to delete this jobcard?')) {
        axiosInstance
            .delete(`/job-card/${jobcardId}`)
            .then((response) => {
                fetchJobCards();
                loading_spinner.value = true;
                alert('Jobcard deleted successfully!');
                if (response.data.status === 200) {
                    fetchJobCards();
                } else {
                    console.error("Error deleting jobcard:", response.data.message);
                }
            })
            .catch((error) => {
                console.error("Error deleting jobcard:", error.message);
            });
    }
};

const companies= ref([])
const company = ref('')
const getCompanies = async()=>{
  const response = await axiosInstance.get('companies')
  companies.value = response.data
}

const asset = ref('')
const getAsset = async()=>{
  const response = await axiosInstance.get(`machines/${route.params.asset_id}`)
  asset.value = response.data
}


const filteredJobcards = ref([])
        watch(company, (newVal) => {
    if (newVal) {
         filteredJobcards.value = items.value.filter(card => card.company_id === newVal.id)
    }else{
      filteredJobcards.value = items.value
    }
});

const expenses = ref('')
const getAssetExpenses = async ()=>{
      const response = await axiosInstance.get(`asset-expenses?asset_id=${route.params.asset_id}`)
      expenses.value = response.data
}
    onMounted(() => {
        getCompanies();
        fetchJobCards();
        getAsset()
        fetchServices();
        getAssetExpenses();
    });
</script>

